import styled, { css } from "styled-components";

import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";

import { StyledButtonProps } from "./Button.types";
import { getPrimaryButtonContentColor, primaryButtonColors } from "./primaryButtonColors";
import { sizedButton } from "./utils";

const borderRadius = `
	border-radius: 22px;
`;

export const sharedButtonStyles = css<Omit<StyledButtonProps, "variant" | "size">>`
	box-sizing: border-box;
	width: auto;
	display: inline-flex;
	align-items: center;
	justify-content: center;

	margin: ${({ m }) => m};
	margin-left: ${({ ml }) => ml};
	margin-right: ${({ mr }) => mr};
	margin-top: ${({ mt }) => mt};
	margin-bottom: ${({ mb }) => mb};

	&:focus {
		outline: none;
		box-shadow: 0 0 0 2px ${colors.white}, 0 0 0 4px ${colors.midBlue};
	};
	&:hover {
		cursor: pointer;
	};
	&:disabled {
		cursor: not-allowed;
		opacity: 50%;
	}
`;

export const StyledButton = styled.button<StyledButtonProps>`
	${sharedButtonStyles};
	${({ size }) => size && sizedButton[size]}

	${({ variant, primaryButtonColor }) => {
		switch (variant) {
			case "primary":
				return primaryButtonColor && `
					background: ${primaryButtonColors[primaryButtonColor].default};
					border: none;
					color: ${getPrimaryButtonContentColor(primaryButtonColor)};
					${borderRadius};

					&:hover {
						background: ${primaryButtonColors[primaryButtonColor].hoverAndActive};
					};
					&:active {
						background: ${primaryButtonColors[primaryButtonColor].hoverAndActive};
						box-shadow:  0 0 0 4px ${primaryButtonColors[primaryButtonColor].default}54;
					};
					&:disabled {
						background: ${primaryButtonColors[primaryButtonColor].default};
						opacity: 50%;
					}
				`;
			case "secondary":
				return `
					background: ${colors.lightGray};
					border: none;
					${borderRadius};

					&:hover {
						background: ${colors.subduedBlueHover};
						color: ${colors.midBlue};
						border: none;
					};

					&:active {
						background: ${colors.subduedBlueHover};
						color: ${colors.midBlue};
						border: none;
						box-shadow:  none;
					};

					&:disabled {
						border: none;
						color: ${colors.black};
						background: ${colors.lightGray};
						opacity: 33%;
					}
				`;
			case "tertiary":
				return `
					border: none;
					outline: none;
					background: ${colors.white};
					${borderRadius};
					text-decoration: underline;

					&:hover {
						background: ${colors.subduedBlueHover};
						color: ${colors.midBlue};
					};
					&:active {
						background: ${colors.subduedBlueHover};
						color: ${colors.midBlue};
						box-shadow:  none;
					};
					&:disabled {
						color: ${colors.black};
						background: ${colors.white};
						opacity: 33%;
					}
				`;
			// default for the switch case can be primary variant
			default:
				return primaryButtonColor && `
				background: ${primaryButtonColors[primaryButtonColor].default};
				border: none;
				color: ${getPrimaryButtonContentColor(primaryButtonColor)};
				${borderRadius};

				&:hover {
					background: ${primaryButtonColors[primaryButtonColor].hoverAndActive};
				};
				&:active {
					background: ${primaryButtonColors[primaryButtonColor].hoverAndActive};
					box-shadow:  0 0 0 4px ${primaryButtonColors[primaryButtonColor].default}54;
				};
				&:disabled {
					background: ${primaryButtonColors[primaryButtonColor].default};
					opacity: 50%;
				}
			`;
		}
	}}
`;

export const ButtonText = styled(Typography)`
	padding: 0 ${spacingMap.xs};
`;

export const IconWrapper = styled.span`
	width: 20px;
	height: 20px;
`;
